import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { LoginRequest } from './model/loginRequest';
import { LoginResponse } from './model/loginresponse';

@Injectable({
  providedIn: 'root'
}) 
export class LoginService {

  //MY_URL: string =  'https://ip-authorization-1612785696210.azurewebsites.net';

  MY_URL: string = 'https://myauth-service.herokuapp.com';//'http://localhost:9192';
 
 
  constructor(private http: HttpClient) { } 

  login(userData: LoginRequest): Observable<LoginResponse> {

    

    return this.http.post<LoginResponse>(
      this.MY_URL+'/auth/login', 
      userData
    )
  } 
}
 